import { DeliveryAddress } from '@sales/delivery/data-access';
import { AddressFieldValue } from '@sales/address-form-field/data-access';
import { AddressUnit } from '@sales/shared/address/data-access';
import { UpdateDeliveryAddressInfo } from '../interfaces/update-installation-address-request.interface';

export class AddressUtil {
  static convertNcAddressToDeliveryAddress(address: AddressUnit): DeliveryAddress {
    return {
      addressUnit: address,
      municipality: this.getAddressValue(address, 'Municipality'),
      postalCode: address.postalCode ? address.postalCode : '',
      street: this.getAddressValue(address, 'Street'),
      houseNumber: address.name,
      subHouseNumber: address.subHouseNumber ? address.subHouseNumber : undefined,
      box: address.boxNumber ? address.boxNumber : undefined,
      country: this.getAddressValue(address, 'Country'),
    };
  }

  static convertNcPostalAddressToDeliveryAddress(address: AddressUnit): DeliveryAddress {
    return {
      addressUnit: address,
      municipality: this.getAddressValue(address, 'Municipality'),
      postalCode: address.name ? address.name : '',
      street: '',
      houseNumber: '',
      subHouseNumber: undefined,
      box: undefined,
      country: this.getAddressValue(address, 'Country'),
    };
  }

  static getUpdateDeliveryLocationBody(addressFieldValue: AddressFieldValue): UpdateDeliveryAddressInfo | undefined {
    if (addressFieldValue.street?.addressUnit) {
      if (!addressFieldValue || !addressFieldValue.geographical_address) {
        return undefined;
      }

      const { geographical_address } = addressFieldValue;

      const { boxNumber = '', floor } = geographical_address.box || {};
      const floorString = floor ? ` (${floor})` : '';
      const boxString = `${boxNumber}${floorString}`;

      return {
        Country: this.getAddressValueId(addressFieldValue.street?.addressUnit, 'Country'),
        'House Number': this.getFieldValueOrDefault(addressFieldValue.geographical_address?.housenumber),
        'Sub-house Number': this.getFieldValueOrDefault(addressFieldValue.geographical_address?.subhousenumber),
        'Box Number': boxString,
        'Municipality/Sub-municipality': this.getAddressValueId(addressFieldValue.street?.addressUnit, 'Municipality'),
        'Postal Code': this.getFieldValueOrDefault(addressFieldValue.municipality?.postalCode),
        Street: addressFieldValue.street?.addressUnit.id,
      };
    }
    return undefined;
  }

  static convertFieldValueToDeliveryAddress(addressFieldValue: AddressFieldValue): DeliveryAddress | undefined {
    if (
      !addressFieldValue ||
      !addressFieldValue.geographical_address ||
      !addressFieldValue.municipality ||
      !addressFieldValue.street
    ) {
      return undefined;
    }

    const { geographical_address, street, municipality } = addressFieldValue;
    if (!street?.addressUnit) {
      return undefined;
    }

    const { boxNumber = '', floor } = geographical_address.box || {};
    const floorString = floor ? ` (${floor})` : '';
    const boxString = `${boxNumber}${floorString}`;

    return {
      addressUnit: street?.addressUnit,
      country: this.getAddressValue(street.addressUnit, 'Country'), // Assuming getAddressValue handles undefined inputs
      postalCode: municipality.postalCode,
      municipality: municipality.location,
      street: street.value || '',
      houseNumber: geographical_address.housenumber || '',
      subHouseNumber: geographical_address.subhousenumber,
      box: boxString,
    };
  }

  static getAddressValue(addressUnit: AddressUnit | undefined, value: string): string {
    if (addressUnit) {
      const foundValue = addressUnit.addressUnitChain?.find((addressUnit) => addressUnit.type === value);
      if (foundValue) {
        return foundValue.name;
      }
    }
    return '';
  }

  static getAddressValueId(addressUnit: AddressUnit, value: string): string {
    const foundValue = addressUnit.addressUnitChain?.find((addressUnit) => addressUnit.type === value);

    if (foundValue) {
      return foundValue.id;
    }
    return '';
  }

  private static getFieldValueOrDefault<T>(fieldValue: T | undefined | null): T | string {
    return fieldValue ?? '';
  }
}
